<template>
  <div>
    <!-- 设置返回按钮 START-->
    <header-nab-back :centerTitle="centerTitle"></header-nab-back>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <login v-if="showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
    </transition>
    <!--设置返回按钮 END-->
    <section class="creation_item">
      <!-- 作品贴头部 -->
      <div class="creation_header">
        <div>
          <a><img :src="workDetail.userAvatar" alt="作品详情作者头像" @click="enterPerCenter(workDetail)"></a>
        </div>
        <div>
          <a>{{workDetail.nickName}}</a>
          <br><span>{{new Date(workDetail.dateAdded) | dateDiff}}</span>
        </div>
        <div>
          <a v-if="account != workDetail.userId" :class="isFollow == 'Y' ? 'isPraise' : 'noPraise'" @click="changeAttentionStatus(workDetail)">
                {{isFollow == 'Y' ? '已关注' : '未关注'}}
              </a>
        </div>
      </div>
      <!-- 作品帖主体内容区域 -->
      <div class="creation_content">
        <p>{{workDetail.content}}</p>
        <ul>
          <li v-for="(workImg, index) in workDetail.contentPics.split(',')" :key="index"><img :src="workImg" alt="作品内容"></li>
        </ul>
      </div>
      <!-- 阅读数 -->
      <div class="creation_read_num">阅读{{workDetail.browseCount}}</div>
    </section>
    <div class="creation_footer">
      <!-- 评论路由 -->
      <router-link tag="div" :to="{name:'workCircleCommentList'}">
        <img src="../../../static/images/形状 5@2x.png" alt="评论"><span>{{workDetail.numberComments}}</span>
      </router-link>
      <!-- 点赞路由 -->
      <router-link tag="div" :to="{name: 'workCirclePraiseList'}">
        <img :src="noPraiseImg" alt="点赞"><span>{{workDetail.numberFabulous}}</span>
      </router-link>
    </div>
    <!-- 点赞 START -->
    <section class="footer">
      <router-view v-on:replayComment="replayComment"></router-view>
    </section>
    <section class="fixed_comment">
      <!-- 评论input -->
      <div><input type="text" v-model="workCircleComment" :placeholder="placeholder"></div>
      <!-- 点赞状态 -->
      <div><img :src="isPraise == 'Y' ? yesPraiseImg : noPraiseImg" alt="点赞状态" @click="changePraiseStatus(workDetail)"></div>
      <!-- 分享按钮 -->
      <div><img :src="shareImg" alt="分享" @click="sendMsg(workDetail)"></div>
    </section>
    <!-- 点赞 END -->
    <loading v-if="showLoading" :title="loadingTitle"></loading>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <topic :title="topicTitle" v-if="showTopic" class="animated"></topic>
    </transition>
  </div>
</template>
<script>
  import headerNabBack from '@/components/headerNavBack'
  import login from '@/components/login'
  export default {
    components: {
      headerNabBack,
      login
    },
    data() {
      return {
        centerTitle: '作品详情',
        workDetail: {
          user: {},
          contentPics: ''
        },
        baseUrl: 'https://test/dapengjiaoyu.com',
        isPraise: 'N',
        noPraiseImg: require('../../assets/img/jef4eada.png'),
        yesPraiseImg: require('../../assets/img/jef4f7vr.png'),
        shareImg: require('../../assets/img/jehyyijh.png'),
        back_img: require('../../assets/img/per_fh.png'),
        // 评论列表
        commentList: new Array(),
        attention: 'Y',
        // 回复内容
        workCircleComment: '',
        replayCommentAccount: Object,
        placeholder: '说两句...',
        isFollow: '',
        account: '',
        showLoginModal: 0
      }
    },
    created() {
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      // 通过worksIs查询作品详情
      this.axios.get('/api/getWorkInfo', {
        params: {
          worksId: this.$route.params.id
        }
      }).then(data => {
        this.account = data.data.userId || ''
        this.isFollow = data.data.result.isFollow || ''
        this.workDetail = data.data.result.WorksE
        if (this.workDetail.userAvatar.indexOf('http') == -1) {
          this.workDetail.userAvatar = `${this.baseUrl}${this.workDetail.userAvatar}`
        }
        // 当前登录用户对该作品的点赞状态
        if (data.data.result.isPraise) {
          this.isPraise = data.data.result.isPraise
        }
        this.$Indicator.close()
      }).catch(err => {
        console.log(err)
      })
      this.$router.push({
        name: 'workCircleCommentList'
      })
    },
    methods: {
      showDialogEvent(data) {
        this.showLoginModal = data
      },
      // 进入个人中心
      enterPerCenter(userInfo) {
        console.log(userInfo)
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: userInfo.userId
          }
        })
      },
      // 改变关注状态
      changeAttentionStatus(workInfo) {
        if (!this.$store.state.user) {
          this.showLoginModal = 1
          return
        }
        if (this.isFollow == 'N') {
          this.axios.post('/api/user-following/add', {
            followingId: workInfo.userId
          }).then(data => {
            if (data.data.result == '关注成功') {
              this.changeTopic('关注成功',500)
              this.$set(workInfo, 'isFollow', 'Y')
            }
            this.isFollow = 'Y'
          }).catch(err => {
            console.log(err)
          })
        } else if (this.isFollow == 'Y') {
          this.axios.post('/api/user-following/delete', {
            followingId: workInfo.userId
          }).then(data => {
            this.isFollow = 'N'
            if (data.data.result == '移除成功') {
              this.changeTopic('取消关注成功',500)
              this.$set(workInfo, 'isFollow', 'N')
            } else if (data.data.result == '移除失败') {
              this.changeTopic('取消关注失败',500)
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      // 回复函数，修改placeholder
      replayComment(info) {
        console.log(info)
        this.replayCommentAccount = info
        this.placeholder = `@${info.user.nickname}`
      },
      // 删除评论
      deletionComment(comment, index, commentList) {
        this.axios.get('/api/user/delectWorksCommentE', {
          params: {
            commentIds: comment.commentE.commentId
          }
        }).then(data => {
          if (data.data.status == 'success') {
            commentList.splice(index, 1)
            this.$Toast({
              message: '删除评论成功',
              position: 'middle',
              duration: 1000
            })
          } else {
            this.$Toast({
              message: '删除评论失败',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 发送评论
      sendMsg(workInfo) {
        if(!this.$store.state.user){
          this.showLoginModal = 1
          return
        }
        if (this.workCircleComment.length == 0 || this.workCircleComment.charAt(0) == '') {
          this.$Toast({
            message: '评论不能为空',
            position: 'middle',
            duration: 1000
          })
          return
        }
        var params = {
          worksId: workInfo.worksId,
          commentsSection: this.workCircleComment,
        }
        if (this.replayCommentAccount) {
          params['commentId'] = this.replayCommentAccount.commentId
        }
        this.axios.get('/api/user/addwordsComments', {
          params
        }).then(data => {
          console.log(data)
          if(data.data && data.data.status == 0){
            this.$Toast({
              message: '请登录后操作',
              position: 'middle',
              duration: 1000
            })
            return
          }
          if (data.data.result == '您发送的内容包含违禁用词，请核对后重新发送！') {
            this.$Toast({
              message: '发送的内容包含违禁用词',
              position: 'middle',
              duration: 1000
            })
            return
          } else if (data.data.result == '新增失败') {
            this.$Toast({
              message: '新增失败',
              position: 'middle',
              duration: 1000
            })
            return
          } else if (data.data.result == '没有该作品信息！') {
            this.$Toast({
              message: '没有该作品信息！',
              position: 'middle',
              duration: 1000
            })
            return
          } else if (data.data.status == 'success') {
            this.$Toast({
              message: '评论成功',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 提示状态Nms
      changeTopic(info, time) {
        this.topicTitle = info
        this.showTopic = 1
        setTimeout(() => {
          this.showTopic = 0
        }, time)
      },
      // 进入个人中心
      enterPerCenter(userInfo) {
        console.log(userInfo)
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: userInfo.userId
          }
        })
      },
      // 点赞以及取消赞
      changePraiseStatus(workInfo) {
        if (!this.$store.state.user) {
          this.showLoginModal = 1
          return
        }
        this.axios.get('/api/likeWorks', {
          params: {
            worksId: workInfo.worksId,
            action: this.isPraise == 'Y' ? 'NORMAL' : 'UPVOTE'
          }
        }).then(data => {
          if (data.data.status === 'success' && data.data.result == '取消点赞 -1！') {
            this.$Toast({
              message: '取消点赞成功',
              position: 'middle',
              duration: 1000
            })
            this.isPraise = 'N'
            this.$set(this.workDetail, 'numberFabulous', --this.workDetail.numberFabulous)
          } else if (data.data.status === 'success' && data.data.result == '点赞 +1！') {
            this.$Toast({
              message: '点赞成功',
              position: 'middle',
              duration: 1000
            })
            this.isPraise = 'Y'
            this.$set(this.workDetail, 'numberFabulous', ++this.workDetail.numberFabulous)
          }
        }).catch(err => {
          console.log(err)
        })
      },
      back() {
        this.$router.push('/workCircle')
      },
    }
  }
</script>


<style lang="less" scoped>
  .fixed_comment {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    height: 3.125rem;
    background-color: #fff;
    &>div:nth-child(1) {
      flex: 7;
      height: 2.5rem;
      line-height: 3.125rem;
      &>input {
        border: none;
        border: 0;
        outline: none;
        height: 2.5rem;
        width: 100%;
        text-indent: 1em;
      }
    }
    &>div:nth-child(2) {
      flex: 1.5;
      height: 2.5rem;
      line-height: 3.125rem;
      &>img {
        width: 1.5625rem;
        vertical-align: middle;
      }
    }
    &>div:nth-child(3) {
      flex: 1.5;
      height: 2.5rem;
      line-height: 3.125rem;
      &>img {
        width: 1.5625rem;
        vertical-align: middle;
      }
    }
  }
  body {
    background-color: #eee;
  }
  .header {
    height: 3.125rem;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 1;
    border-bottom: .0625rem solid rgba(0, 0, 0, .1);
    div {
      padding-left: 1.25rem;
      height: 2.5rem;
      vertical-align: middle;
      img {
        width: .875rem;
        vertical-align: middle;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    span {
      vertical-align: middle;
      display: inline-block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  } // 头部
  // 作品的具体内容
  .creation_item {
    margin-top: .625rem;
    background-color: #fff;
    .creation_header {
      display: flex;
      &>div:nth-child(1) {
        -webkit-box-flex: 2;
        flex: 2;
        padding: .3125rem 0 0 .625rem;
        img {
          width: 3.75rem;
          height: 3.75rem;
        }
      }
      &>div:nth-child(2) {
        -webkit-box-flex: 6;
        flex: 6;
        padding-top: .9375rem;
        span {
          color: #aaa;
        }
      }
      &>div:nth-child(3) {
        -webkit-box-flex: 2;
        flex: 2;
        a {
          display: block;
          width: 80%;
          height: 40%;
          border: 1px solid #aaa !important;
          margin-top: .9375rem;
          text-align: center;
          border-radius: .625rem;
          padding: .3125rem 0;
        }
      }
    } // 作品详情内容
    .creation_content {
      box-sizing: border-box;
      p {
        width: 100%;
        padding: .625rem 0 .625rem 1.25rem;
        box-sizing: border-box;
      }
      ul {
        box-sizing: border-box;
        width: 100%;
        padding: .625rem 1.25rem .625rem 1.25rem;
        li {
          margin: .3125rem 0;
          &>img {
            width: 100%;
          }
        }
      }
    }
    .creation_read_num {
      width: 100%;
      padding: .625rem 0 1.25rem 1.25rem;
      box-sizing: border-box;
      color: #aaa;
    }
  }
  .creation_footer {
    margin-top: .625rem;
    background-color: #fff;
    display: flex;
    padding: .3125rem 0;
    border-bottom: 1px solid #aaa;
    box-sizing: border-box;
    div {
      flex: 1;
      text-align: center;
      height: 1.875rem;
      line-height: 1.875rem;
      img {
        vertical-align: middle;
        width: 1.25rem;
        height: 1.25rem;
      }
      span {
        vertical-align: middle;
        margin-left: .625rem;
      }
    }
  } // 底部评论和点赞 START
  .mutual {
    display: flex;
    box-sizing: border-box;
    background-color: #fff;
    div {
      flex: 1;
      box-sizing: border-box;
    }
    div:nth-child(1) {
      padding: .625rem 0 0 .625rem;
      a {
        img {
          width: 3.75rem;
          height: 3.75rem;
          vertical-align: middle;
        }
        span {
          vertical-align: middle;
          color: #3fb140;
        }
      }
    }
    div:nth-child(2) {
      color: #aaa;
      text-align: right;
      padding: 1.875rem 1.25rem 0 0;
    }
  } // 底部评论和点赞 END
  // isPraise
  .isPraise {
    background-color: #44b336;
    color: #fff;
  } // 删除评论
  .delet-comment {
    margin-top: .625rem;
  }
</style>



